#background {
  background-color: #4d4d4d;
  color: white;
}

#skill-rank-dot-open {
  border: 1px ridge black;
  border-radius: 50%; width: 12px; height: 12px;
}

#skill-rank-dot-filled {
  border: 1px ridge black;
  background-color: gold;
  border-radius: 50%; width: 12px; height: 12px;
}

#empty-item-48 {
  display: inline-block;
  width: 48px;
  height: 48px;
  background: #333333;
  margin: 1px;
}

#empty-item-64 {
  display: inline-block;
  width: 64px;
  height: 64px;
  background: #333333;
}

#img-48 {
  width: 48px;
  height: 48px;
  vertical-align: top;
}

#item-data {
  width: 150px;
}

#settings-label {
  width: 150px;
}

#settings-box {
  margin: 2px;
  width: 150px;
}

.help-modal {
  width: 50%;
  min-width: 50%;
  max-width: 50%;
} 